.konamied {
  transform: rotate(180deg);
  background-image: url('http://static.blog.playstation.com/wp-content/uploads/avatars//Old-Konami-Logo.jpg?m=1414759874');
}
section {
  margin-bottom: 3em;
}
h3 {
  font-size: 2rem;
}
ul {
  list-style: none;
}
li {
  line-height: 2;
}
footer {
  margin-top: 6%;
}
@media only screen and (max-width: 640px) {
  header h1 {
    font-size: 3rem;
  }
  header img {
    width: 100%;
  }
}
